  font-family: Arial, sans-serif;
  margin: 0;
  padding: 20px;
}

h1 {
  text-align: center;
}

#todo-list {
  width: 40%;
  box-shadow: 1px 2px 8px rgb(152, 150, 150);
  border-radius: 4px;
  margin: 50px auto;
}

ul {
  list-style-type: none;
  padding: 0;
  margin-bottom: -10px;
}

li {
  margin-bottom: 10px;
  padding-left: 10px;
  padding-bottom: 15px;
  position: relative;
  border-bottom: 1px solid #ccc;
}

h3 {
  padding: 15px 10px;
  position: relative;
  border-bottom: 1px solid #ccc;
}

h3::before {
  content: "";
  position: absolute;
  top: 30%;
  right: 2%;
  width: 20px;
  height: 20px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  cursor: pointer;
}

li.selected {
  background-color: yellow;
}

.icon {
  width: 24px;
  height: 24px;
  cursor: pointer;
}

.trash-icon {
  display: none;
}

li.selected .icon {
  display: none;
}

li.selected .trash-icon {
  display: inline-block;
}

.kebab-menu {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: cover;
  width: 24px;
  height: 24px;
  position: absolute;
  top: 0;
  right: 36px;
  cursor: pointer;
}

.trash-can-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: cover;
  width: 24px;
  height: 24px;
  position: absolute;
  top: 0;
  right: 10px;
  cursor: pointer;
}

input[type=checkbox] {
  margin-right: 15px;
}

#task-input {
  width: 97%;
  padding-left: 12px;
  padding-bottom: 12px;
  font-size: 16px;
  border: none;
  border-bottom: 1px solid #ccc;
  outline: none;
}

#clear-button {
  border: none;
  background-color: rgb(235, 235, 235);
  width: 100%;
  padding: 15px;
  margin-top: 0;
  cursor: pointer;
  font-size: 16px;
}

#clear-button:hover {
  background-color: rgb(210, 208, 208);
