import kebabIcon from "./assets/kebab-menu.png";
import trashIcon from "./assets/trash-can-icon.png";
import {
  tasks,
  deleteTask,
  updateTaskIndexes,
  saveTasksToLocalStorage,
} from "./taskManager";

export function renderTasks() {
  const deleteTaskHandler = (index) => {
    deleteTask(index);
  };

  const taskList = document.getElementById("task-list");
  taskList.innerHTML = "";

  tasks.forEach((task, index) => {
    const listItem = document.createElement("li");
    listItem.draggable = true;

    const checkbox = document.createElement("input");
    checkbox.type = "checkbox";
    checkbox.checked = task.completed;
    checkbox.addEventListener("change", () => {
      tasks[index].completed = checkbox.checked;
      renderTasks();
      saveTasksToLocalStorage();
    });

    const label = document.createElement("label");
    label.innerText = task.description;
    label.style.textDecoration = task.completed ? "line-through" : "none";

    listItem.addEventListener("click", () => {
      listItem.classList.toggle("selected");
    });

    const kebabMenu = document.createElement("div");
    kebabMenu.className = "kebab-menu";

    const kebabIconImg = document.createElement("img");
    kebabIconImg.src = kebabIcon;
    kebabIconImg.alt = "Kebab Icon";
    kebabIconImg.className = "icon";
    kebabIconImg.addEventListener("click", (event) => {
      event.stopPropagation();
      listItem.classList.toggle("selected");
      renderTasks();
    });

    const trashIconImg = document.createElement("img");
    trashIconImg.src = trashIcon;
    trashIconImg.alt = "Trash Can Icon";
    trashIconImg.className = "icon trash-icon";
    trashIconImg.addEventListener("click", (event) => {
      event.stopPropagation();
      deleteTaskHandler(index);
    });

    if (task.completed) {
      kebabIconImg.style.display = "none";
    }

    kebabMenu.appendChild(kebabIconImg);
    kebabMenu.appendChild(trashIconImg);

    listItem.appendChild(kebabMenu);
    listItem.appendChild(checkbox);
    listItem.appendChild(label);
    taskList.appendChild(listItem);
  });
}
